import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
// import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import InfoCard from './InfoCard/InfoCard'
import ResearchStatementCard from './ResearchStatementCard/ResearchStatementCard'

import Publications from '../Publications/Publications'


const styles = theme => ({
  root: {
    flexGrow: 1,
    padding: theme.spacing(2),
    textAlign: 'left',
    alignContent: 'flex-start'
  },
//   paper: {
//     padding: theme.spacing(2),
//     textAlign: 'center',
//     color: theme.palette.text.secondary,
//   },
});

function MainLayout(props) {
  const { classes } = props;

  return (
    <div >
      <Grid container className={classes.root}>
        <Grid item >
            <InfoCard />
        </Grid>         
        <Grid item style={{width:650+'px','padding-left':10+'px'}}>
            <ResearchStatementCard />
        </Grid> 
        
        {/* Current research */}
        
        <Grid item  className='left' id='current_research'>
            <h1>Current Research</h1>
            <Publications publications = {props.current_research} />
        </Grid> 

         {/* Past research */}
        
        <Grid item  className='left' >
            <h1>Past Research</h1>
            <Publications publications = {props.past_research} />
        </Grid> 
        
      </Grid>
    </div>
  );
}

MainLayout.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MainLayout);