import React from 'react'
import Card from '@material-ui/core/Card';
import { CardContent, Link } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import CardMedia from '@material-ui/core/CardMedia';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import CardActionArea from '@material-ui/core/CardActionArea';

import IconhkScreenshot from '../../../assets/img/Giannisakis2017Iconhk.png'
import Bailly2018Transition from '../../../assets/img/Bailly2018Transition.png'
import Boukhelifa2015HistoricalResearch from '../../../assets/img/Boukhelifa2015HistoricalResearch.png'
import Giannisakis2015Playware from '../../../assets/img/Giannisakis2015Playware.png'
import Giannisakis2017Iconhk from '../../../assets/img/Giannisakis2017Iconhk.png'



const useStyles = makeStyles (theme =>({
    card: {
      maxWidth: 100,
    },
    media: {
      height: 110,
    },
    link: {
      margin: '10px',
    },
    publication:{
        text:{
            width:530
        }
    },
    root: {
        flexGrow: 1,
      },
      Card: {
        height: 140,
        width: 100,
      },
      control: {
        padding: theme.spacing(2),
      },
      item:{
        //   height:150
      }
  }));


function Publication(props) {
    console.log(props.title);

    const images ={
        'Bailly2018Transition':Bailly2018Transition,
        'Boukhelifa2015HistoricalResearch':Boukhelifa2015HistoricalResearch,
        'Giannisakis2015Playware':Giannisakis2015Playware,
        'Giannisakis2017Iconhk':Giannisakis2017Iconhk,
    }
    console.log('=====>',images,props.image,images[props.image],props)
    const classes = useStyles();

    const routeTo = (pdflink)=>{
        console.log('hello biatches:',pdflink)
        window.open(pdflink); //This will open Google in a new       
    }
    console.log('this',this)

    return (
        <Grid container className={classes.root} spacing={2}> 
            <Grid item className={classes.item}>
                <Card onClick={()=> routeTo(props.pdflink)}>
                    <CardActionArea>
                        <CardMedia
                            className={classes.media}
                            component = 'img'
                            // img={require('../../../assets/img/Bailly2018Transition.png')}
                            image={images[props.image]}
                            title="Profile picture of Emmanouil Giannisakis"
                            alt="Profile picture of Emmanouil Giannisakis"
                        /> 
                    </CardActionArea>
                </Card>
            </Grid>
            <Grid item className={classes.item}>
                <Card className={classes.publication.text} elevation={0}>
                    <CardContent>
                        <Typography color='textPrimary'>
                            {props.title}
                        </Typography>
                        <Typography color='textSecondary'>
                            {props.authors.map(x =>{
                                let t = '_blank';
                                if (x.link =='#' || x==''){
                                    t=''
                                    x.link = '#current_research'
                                }
                                return <Link underline="always" color='inherit' target={t} rel='noreferrer' href={x.link} style={{marginRight: 0.5 + 'em'}}> {x.name} </Link>
                            })}
                        </Typography>
                        <Typography color='textSecondary'>
                            {props.venue}, {props.year}
                        </Typography>
                    </CardContent>
                </Card>
            </Grid>
        </Grid>
       
        
    )
}

export default Publication;